.footerContainer{
    font-size: 12px;
    padding-bottom: 20px;
    text-align: center;
}

.footerContainer a {
    display: inline-block;
    text-decoration: none;
    color: grey;
    cursor: pointer;
    padding: 0 20px;
    margin: 20px 0;
    line-height: 10px;
}

.footerContainer span{
    color: grey;
    padding: 0 20px;
    margin: 20px 0;
}

.footerContainer a:nth-of-type(odd){
    border-right: 1px solid grey;
}