.menuContainer {
    background: linear-gradient(#ffffff, #b5b3eb);
    margin: 0;
    padding: 0;
    width: 100%;
}

.menuContainer>li {
    list-style: none;
    text-align: center;
    padding: 0;
}

.menuContainer>li:hover{
    background-color: #fff;
}

.menuContainer>li>a{
    font-size: 16px;
    line-height: 45px;
    display: block;
    color: black;
    width: 100%;
    text-decoration: none;
    font-weight: 600;
}