.symbolListContainer {
}

ul{
    padding: 0;
}

.symbolStyle {
    font-size: 13px;
    padding: 5px 0;
    cursor: pointer;
}

.symbolStyle:hover {
    filter: brightness(85%);
}

.textRed {
    color: red;
}

.textGreen {
    color: green;
}

.playIcon>span {
    color: #fff;
    background-color: #716fb3;
    padding: 5px;
    font-size: 8px;
    border-radius: 10%;
    cursor: pointer;
    margin-right: 5px;
}

.symbolsContainer {
    max-height: 400px;
    overflow-y: auto;
    overflow-x: hidden;
    outline: none;
}

.symbolsContainer::-webkit-scrollbar{
    width: 5px;
    background-color: #f3f4f8;
}

.symbolsContainer::-webkit-scrollbar-thumb{
    background-color: #716fb3;
}