header {
    padding-top: 10px;
}

.learnLinks>div.linkContainer {
    padding-top: 5px;
}

.learnLinks a{
    text-decoration: none;
    font-size: 10px;
    text-align: center;
    color: #aca7e1;
}

.learnLinks>div:nth-of-type(3) a {
    padding: 0 13px;
    border-right: 1px solid #aca7e1;
    border-left: 1px solid #aca7e1;
}

.learnLinks>div {
    padding: 0;
    text-align: center;
}

.learnLinks h1 {
    margin-right: 20px;
    font-size: 20px;
    text-align: right;
    color: #aca7e1;
}

a:hover{
    filter: brightness(85%);
}


.categories {
    background-color: #000;
    height: auto;
    font-size: 10px;
    color: #fff
}

.categories p {
    line-height: 3px;
}

